import React, {useEffect, useState} from "react";

const CountDown = () => {
  const countDownDate = new Date("April 30, 2022 20:00:00").getTime();
  const [days, setDays] = useState(0);
  const [hours, setHours] = useState(0);
  const [minutes, setMinutes] = useState(0);
  const [seconds, setSeconds] = useState(0);
  useEffect(() => {
    setInterval(() => {
      // Get today's date and time
      let now = new Date().getTime();
      
      // Find the distance between now and the count down date
      let distance = countDownDate - now;
      
      // Time calculations for days, hours, minutes and seconds
      let days = Math.floor(distance / (1000 * 60 * 60 * 24));
      let hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      let minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
      let seconds = Math.floor((distance % (1000 * 60)) / 1000);
      
      setDays(days);
      setHours(hours);
      setMinutes(minutes);
      setSeconds(seconds);
      
      // If the count down is over, write some text
      if (distance < 0) {
        // clearInterval(countdown);
        setDays(0);
        setHours(0);
        setMinutes(0);
        setSeconds(0);
      }
    }, 1000);
  });
  return (
    <h4 className="countdown-time">
      <span style={{color: "#9b59b6"}}>{days >= 10 ? days : `0${days}`} days - {hours >= 10 ? hours : `0${hours}`}:{minutes >= 10 ? minutes : `0${minutes}`}:{seconds >= 10 ? seconds : `0${seconds}`} </span>
      left
    </h4>
  );
};

export default CountDown;